import React from "react";
import { withPrefix } from "gatsby";

import Layout from "../components/Layout";
import Sidebar from "../components/Sidebar";
import config from "../../config";

import camp from "../assets/images/rohingyacamp.jpg";

export default ({ data }) => (
  <Layout>
    <Sidebar />
    <div className="container-fluid p-0">
      <section
        className="resume-section p-lg-5 p-md-4 d-flex align-items-center"
        id="about"
      >
        <div className="w-100">
          <h1 className="mb-0">
            {config.firstName}
            <span className="text-primary">{config.lastName}</span>
          </h1>
          <div className="subheading mb-5">
            {config.address}  {config.phone}  {config.email} .{" "}
            {"She/ Her/ Hers"}
          </div>
          <div className="intro-container">
            <div className="introduction">
              <p className="lead">
                Hi, I am Farhana from Bangladesh! I rethink the design and use
                of technologies to address the needs of low-resource
                communities.
                <br />
                <br />I am an incoming PhD student in{" "}
                <a
                  href="https://infosci.cornell.edu/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Cornell Information Science
                </a>{" "}
                working with Professor{" "}
                <a
                  href="https://www.adityavashistha.com/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Aditya Vashistha
                </a>
                . My research focuses on the design, use, and adaptation of
                existing technologies to meet the needs of underserved
                communities for upholding their right to digital inclusion. My
                goal is not only to solve problems. Rather I aspire to bring
                solutions that are{" "}
                <span className="sym-icon">accessible, adaptable,</span> and{" "}
                <span className="sym-icon">acceptable</span> to the communities
                I am working for.
                <br />
                <br />
                Before coming to the US, I obtained both my BSc and MSc degrees
                in Computer Science and Engineering from{" "}
                <a
                  href="https://cse.buet.ac.bd/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  BUET
                </a>
                , where I worked with Professor{" "}
                <a
                  href="https://sites.google.com/site/abmalimalislam/home"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  A. B. M. Alim Al Islam
                </a>{" "}
                at the intersection of HCI and Ubiquitous Computing. Besides, I
                spent around 4 wonderful years at{" "}
                <a
                  href="https://www.bracu.ac.bd/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Brac University
                </a>{" "}
                and{" "}
                <a
                  href="https://www.easternuni.edu.bd/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Eastern University
                </a>{" "}
                serving as a lecturer of CSE, where I closely worked with
                undergraduate students.
                <br />
                <br />
                Apart from research and teaching, music-and-book greatly stir my
                interests. It really appeals to me whenever books and music
                complement one another and radiate similar vibe. As part of my{" "}
                <span className="sym-icon">#goodreads</span> reading challenge,
                I have already finished reading 36 books (~11k+ pgs) since
                January. Now, I am earnestly looking forward to the warmth of
                Summer readings.
                <br />
                <br />
                <a
                  href={withPrefix("/files/FSH CV.pdf")}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                <b>Download CV</b>
                </a>{" "}
              </p>

              <div className="social-icons">
                {config.socialLinks.map((social) => {
                  const { icon, url } = social;
                  return (
                    <a
                      key={url}
                      href={url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <i className={icon}></i>
                    </a>
                  );
                })}
              </div>
            </div>

            <div className="news">
              <span className="recNews">
                <b>Recent News</b>
              </span>
              <ul class="fa-ul">
                <li>
                  <span class="fa-li">
                    <i class="fas fa-graduation-cap"></i>
                  </span>
                  <b>May 21</b>: Defended MSc thesis at BUET!
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-hands-helping"></i>
                  </span>
                  <b>May 21</b>: Volunteered at CHI2021!
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-chalkboard-teacher"></i>
                  </span>
                  <b>Oct 20</b>: Presented our paper on dengue epidemic in
                  Bangladesh at CSCW!
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-award"></i>
                  </span>
                  <b>Oct 20</b>: Our work on 2019 Dengue epidemic in Bangladesh
                  won{" "}
                  <span className="authorMe">CSCW Honorable Mention Award</span>
                  !
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-award"></i>
                  </span>
                  <b>Sep 20</b>: Our work on PTSD among Rohingya refugees won{" "}
                  <span className="authorMe">
                    IMWUT Distinguished Paper Award
                  </span>
                  !
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-chalkboard-teacher"></i>
                  </span>
                  <b>Sep 20</b>: Presented our paper on PTSD and free-hand
                  sketches at UbiComp!
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-pen-square"></i>
                  </span>
                  <b>Jul 20</b>: Paper on PTSD and free-hand sketches accepted
                  at IMWUT!
                </li>
                <li>
                  <span class="fa-li">
                    <i class="fas fa-pen-square"></i>
                  </span>
                  <b>Apr 20</b>: Paper on dengue epidemic accepted at CSCW!
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-lg-5 p-md-4 d-flex justify-content-center"
        id="publication"
      >
        <div className="w-100">
          <h2 className="mb-5">Publications</h2>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">2021</h3>
              <ul className="papers">
                <li className="paper">
                  <a
                    href={withPrefix("/files/FSH_MSc_Thesis.pdf")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Leveraging Features from EEG and Free-Hand Sketches for
                    Effective Diagnosis of Post-Traumatic Stress Disorder.
                  </a>
                  <br />
                  <span className="authorMe">Farhana Shahid</span>. M.Sc. Engg.
                  Thesis, Department of Computer Science and Engineering, BUET.
                </li>
              </ul>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">2020</h3>
              <ul className="papers">
                <li className="paper">
                  <a
                    href="https://dl.acm.org/doi/10.1145/3411835"
                    target="_blank"
                    rel="noopener
	          noreferrer"
                  >
                    Leveraging Free-Hand Sketches for Potential Screening of
                    PTSD.
                  </a>
                  <br />
                  <span className="authorMe">Farhana Shahid</span>, Wasifur
                  Rahman, M. Saifur Rahman, Sharmin Akther Purabi, Ayesha
                  Seddiqa, Moin Mostakim, Farhan Feroz, Tanjir Rashid Soron,
                  Fahmida Hossain, Nabila Khan, Anika Binte Islam, Nipi Paul,
                  Ehsan Hoque, and A. B. M. Alim Al Islam. In{" "}
                  <span className="publishedwhere">
                    {" "}
                    Proceedings of the ACM on Interactive Mobile, Wearable and
                    Ubiquitous Technologies (IMWUT' 20)
                  </span>
                  : Vol. 4, No. 3, Article 94. [
                  <a
                    href={withPrefix("/files/Sketch_Camera_Ready.pdf")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    PDF
                  </a>
                  ] [
                  <a
                    href={withPrefix("/files/UbiComp20_Sketch.pptx")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    UbiComp'20 Presentation
                  </a>
                  ] [
                  <a
                    href={"https://youtu.be/-L8GdW69AFU"}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Video
                  </a>
                  ]
                </li>
                <br />

                <li className="paper">
                  <a
                    href="https://dl.acm.org/doi/10.1145/3392875"
                    target="_blank"
                    rel="noopener
	          noreferrer"
                  >
                    Learning from Tweets: Opportunities and Challenges to Inform
                    Policy Making During Dengue Epidemic.
                  </a>
                  <br />
                  <span className="authorMe">Farhana Shahid</span>, Shahinul
                  Hoque Ony, Takrim Rahman Albi, Sriram Chellappan, Aditya
                  Vashistha, and A. B. M. Alim Al Islam. In{" "}
                  <span className="publishedwhere">
                    {" "}
                    Proceedings of the ACM on Human-Computer Interaction
                  </span>
                  : Vol. 4, Issue CSCW1, Article 65. [
                  <a
                    href={withPrefix("/files/cscw_dengue_2020.pdf")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    PDF
                  </a>
                  ] [
                  <a
                    href={withPrefix("/files/CSCW20_Presentation.pptx")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    CSCW'20 Presentation
                  </a>
                  ] [
                  <a
                    href={"https://youtu.be/Oe0fD_LwpyY"}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Video
                  </a>
                  ]
                </li>
                <span class="sym-icon">
                  <i class="fas fa-award"></i>
                  <b> CSCW Honorable Mention Award</b>
                </span>
              </ul>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between">
            <div className="resume-content">
              <h3 className="mb-0">2019</h3>
              <ul className="papers">
                <li className="paper">
                  <a
                    href="https://dl.acm.org/doi/10.1145/3351259"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Two Tell-tale Perspectives of PTSD: Neurobiological
                    Abnormalities and Bayesian Regulatory Network of the
                    Underlying Disorder in a Refugee Context.
                  </a>
                  <br />
                  <span className="authorMe">Farhana Shahid</span>, Wasifur
                  Rahman, Anika Binte Islam, Nipi Paul, Nabila Khan, Mohammad
                  Saifur Rahman, Md Munirul Haque, and A. B. M. Alim Al Islam.
                  In{" "}
                  <span className="publishedwhere">
                    Proceedings of the ACM on Interactive Mobile, Wearable and
                    Ubiquitous Technologies (IMWUT' 19)
                  </span>
                  : Vol. 3, No. 3, Article 101. [
                  <a
                    href={withPrefix("/files/FSH_IMWUT19.pdf")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    PDF
                  </a>
                  ] [
                  <a
                    href={withPrefix("/files/UbiComp19_PTSD.pptx")}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    UbiComp'19 Presentation
                  </a>
                  ]
                </li>
                <span class="sym-icon">
                  <i class="fas fa-award"></i>
                  <b> IMWUT Distinguished Paper Award</b>
                </span>
              </ul>
            </div>
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-lg-5 p-md-4 d-flex align-items-center"
        id="research"
      >
        <div className="w-100">
          <h2 className="mb-5">Research</h2>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-3">PTSD within Low-Resource Communities</h3>
              <div className="paper-container">
                <div className="paper-body">
                  <p className="paper">
                    Critical lack of trained mental health professionals,
                    communication and cultural barriers, social stigma around
                    mental health in low-rosurce communities expose the
                    traumatized individuals to greater health risks. Traditional
                    diagnostic tools of PTSD often suffer from under-utilization
                    due to various issues associated with human-human
                    interactions while applying them within resource-scarce
                    populations. In this regard, we aim to explore alternative
                    methods to potentially screen for PTSD using various
                    low-cost, off-the-shelf tools, e.g., portable EEG headsets
                    and free-hand sketches using simple pencil and paper.
                    Findings from our field work involving refugees and
                    slum-dwellers enable us to identify scopes for improvement
                    in screening the potential cases of PTSD within low-rosurce
                    communities.
                    <br />
                    Publications:{" "}
                    <a
                      href={withPrefix("/files/FSH_IMWUT19.pdf")}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      IMWUT 2019
                    </a>
                    ,
                    <a
                      href={withPrefix("/files/Sketch_Camera_Ready.pdf")}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {" "}
                      IMWUT 2020
                    </a>
                  </p>
                </div>
                <div className="paper-image">
                  <img
                    className="img-fluid"
                    src={camp}
                    alt="Some female Rohingya refugees are waiting in a room for interview in the camp area"
                  />
                  <p className="img-caption">
                    Kutupalong Rohingya refugee camp, Cox's Bazar, Bangladesh
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-lg-5 p-md-4 d-flex align-items-center"
        id="teaching"
      >
        <div className="w-100">
          <h2 className="mb-5">Teaching</h2>

          <h3 className="mb-5">Appointments</h3>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h4 className="mb-0">Lecturer</h4>
              <div className="subheading mb-3">
                Department of Computer Science and Engineering, Brac University
              </div>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">May 2018 - May 2021</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h4 className="mb-0">Lecturer</h4>
              <div className="subheading mb-3">
                Department of Computer Science and Engineering, Eastern
                University
              </div>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">September 2017 - April 2018</span>
            </div>
          </div>

          <h3 className="mb-5">Courses Taught</h3>

          <ul className="fa-ul mb-0">
            <li>
              <i className="fa-li fa fa-check"></i>
              Computer Graphics (Spring 2021, Fall 2020, Summer 2020, Spring
              2020, Fall 2019, Summer 2019, Spring 2019, Spring 2018)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Pattern Recognition (Fall 2020, Summer 2020)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Artificial Intelligence (Spring 2020, Fall 2019)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Object Oriented Programming (Fall 2019, Summer 2019, Spring 2019,
              Fall 2018, Summer 2018)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Digital Logic Design (Spring 2020, Summer 2018)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Algorithms (Summer 2019, Fall 2019, Summer 2018)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Data Structures (Summer 2019, Summer 2018)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Operating Systems (Spring 2018, Fall 2017)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Mathematical Analysis for Computer Science (Fall 2017)
            </li>
          </ul>
          <br />
        </div>
      </section>
    </div>
  </Layout>
);
